.card{
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    padding: 1rem;
    width: 300px;
    height: 650px;
    background-color: whitesmoke;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
    border-radius: 12px;
    border: 1px solid #ccc;
    text-align: center;
}





